@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: poppins;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.blur {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.add-button {
  position: fixed;
  bottom: 30px;
  right: 25px;
  z-index: 10;
  cursor: pointer;
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
}

.add-button:hover {
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.add-button:active {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.add-note-screen {
  background-color: rgba(0, 0, 0, 0.4);
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 20;
  padding: 50px;
}

.add-note-container {
  background-color: white;
  width: 100%;
  min-height: 100%;
  border-radius: 10px;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.title-bar {
  display: -webkit-flex;
  display: flex;
  height: 35px;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
}

.tick {
  height: 35px;
  width: 35px;
  cursor: pointer;
}

.tick:hover {
  -webkit-filter: brightness(95%);
          filter: brightness(95%);
}

.tick:active {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.name-input {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 100%;
  background-color: #008ddc34;
  margin-right: 5px;
  border-radius: 5px;
  padding: 10px;
}

.body-input {
  width: 100%;
  background-color: #008ddc34;
  border-radius: 5px;
  padding: 10px 10px;
  margin-top: 5px;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
}

.input {
  font-size: 1.2em;
  color: #008ddc;
  width: 100%;
  background-color: transparent;
  border: none;
  font-family: poppins;
}

.input::-webkit-input-placeholder {
  color: #008ddc;
}

.input:-ms-input-placeholder {
  color: #008ddc;
}

.input::placeholder {
  color: #008ddc;
}

.input:focus {
  outline: none;
}

.body-input textarea {
  -webkit-flex: 1 1;
          flex: 1 1;
  resize: none;
}

.container {
  min-height: 100%;
  width: 100%;
  background-color: white;
  border: solid #cccccc 1px;
}

header {
  padding: 20px;
  padding-top: 10px;
}

header h1 {
  color: #008ddc;
}

.search {
  background-color: #008ddc34;
  width: 100%;
  height: 35px;
  border-radius: 30px;
  padding: 5px;
  padding-left: 15px;
  padding-right: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.search input {
  width: 100%;
  font-size: 1.2em;
  color: #008ddc;
  background-color: transparent;
  border: none;
}

.search input::-webkit-input-placeholder {
  color: #008ddc;
}

.search input:-ms-input-placeholder {
  color: #008ddc;
}

.search input::placeholder {
  color: #008ddc;
}

.search input:focus {
  outline: none;
}

.main-body {
  padding: 20px;
}

.body-heading h3 {
  color: #595959;
}

.under-decor {
  background-color: #59595959;
  height: 4px;
  width: 30px;
  border-radius: 4px;
}

.note-list {
  padding: 20px 0px 60px 0px;
}

.head {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.head svg:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  cursor: pointer;
}

.note-name {
  color: #323232;
}

.note-body {
  color: #505050;
}

.App {
  background-color: #008ddc34;
  min-height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

